IG$.cVis.singleseries = $s.extend(IG$.cVis.cartesian, {
	charttype: "pie",
	subtype: null,
	drawmode: "echart",
	
	initComponent: function() {
		var me = this;
		IG$.cVis.singleseries.superclass.initComponent.apply(me, arguments);
	},
	
	draw: function(mresult) {
		var me = this,
			coptions = [],
			i,
			j,
			series,
			bpolarchart = 0,
			mcr = me.chart_option,
			rcs = mcr.subtype,
			cf = [],
			dl_tooltip = mcr.dl_tooltip,
			sep = me.getSep(),
			coption,
			spacingBottom;
			
		me._before_draw(mresult);
			
		coption = me._create_option(mresult);
		
		series = me._prep_series_data(mresult);
		
		var cgrid,
			cchart,
			cyAxis;
		
		cchart = coption.chart;
		cgrid = coption.grid;
		ctitle = coption.title;
		
		spacingBottom = cchart.spacingBottom;
		
		if (mcr.enabledragselect)
		{
			me._enable_drag_select(coption);
		}
		else if (mcr.enablezoom)
		{
			me._enable_zoom(coption);
		} 
			
		me._createAxis(coption);

		mcr.e3d_en === true && me._enable_3d(coption);

		me._createYAxis(coption);
		
		me._draw_legend(coption);

		var options3d = cchart ? cchart.options3d : null;
			
		if (/(pie|pie3d|doughnut|funnel|pyramid)/.test(rcs))
		{
			if (dl_tooltip)
			{
				ctooltip = coption.tooltip = {
					formatter: function(params, ticket, callback) {
						var pct = params.percent;
						
						if (mcr)
						{
							var piepctprec = mcr.piepctprec;
							
							if (typeof(piepctprec) != "undefined" && piepctprec > -1 && piepctprec < 2)
							{
								pcval = pct * Math.pow(10, piepctprec);
								pcval = Math.round(pcval);
								pcval = pcval / Math.pow(10, piepctprec);
								
								pct = pcval;
							}
						}
						
						return params.name + "<br>" + params.data.fval + " (" + pct + " %)";
					}
				};
			}
			
			if (options3d && options3d.enabled)
			{
				delete options3d["viewDistance"];
			}

			cchart.renderTo = null;

			if (!mcr.pielayout)
			{
				series.splice(1, series.length - 1);
			}
			
			var series_len = series.length;
			
			var ocnt = $(me.container),
				owidth = IG$.j$ext._w(ocnt),
				oheight = IG$.j$ext._h(ocnt),
				flex_info = {
					cols: 0,
					margin: 0
				};
				
			if (series_len > 1 && mcr.pielayout == "f")
			{
				mcr.pieflex_width = mcr.pieflex_width || 200;
				flex_info.size = Math.min(mcr.pieflex_width, owidth);
				flex_info.cols = Math.floor(owidth / flex_info.size);
				flex_info.margin = (owidth - flex_info.size * flex_info.cols) / 2;
				me.p_off_left = flex_info.margin;
				
				$(me.container).css({
					overflowX: "hidden",
					overflowY: "auto"
				});
			}
			else
			{
				$(me.container).css({
					overflowX: "hidden",
					overflowY: "hidden"
				});
			}

			$.each(series, function(k, serie) {
				var cnt = $("<div class='i-sschart-cnt'></div>").appendTo(me.container),
					ow = owidth / series.length,
					oh = oheight / series.length,
					toption = $.extend(true, {}, coption),
					i, csv,
					mw = 0,
					mtitle;
				
				/* */
				if (me.rcs == "doughnut")
				{
					serie.emphasis = {
						label: {
							show: true,
							fontSize: 20,
							position: "center"
						}
					};
					
					serie.labelLine = {
						show: true
					};
					
					serie.label = serie.label || {};
					serie.label.show = false;
					serie.label.position = "center";
				}
				/**/
				
				if (toption.title && toption.title.text)
				{
					mtitle = toption.title.text;
					mtitle = IG$.replaceAll(mtitle, "{SERIES}", serie.name);
					toption.title.text = mtitle;
				}
				
				if (mcr.pielayout == "h")
				{
					IG$.j$ext._w(cnt, ow);
					IG$.j$ext._h(cnt, oheight);
					cnt.css({left: me.p_off});
					me.p_off += ow;
				}
				else if (mcr.pielayout == "v")
				{
					IG$.j$ext._h(cnt, oh);
					IG$.j$ext._w(cnt, owidth);
					cnt.css({top: me.p_off});
					me.p_off += oh;
				}
				else if (mcr.pielayout == "f")
				{
					if (series_len > 1)
					{
						IG$.j$ext._h(cnt, flex_info.size);
						IG$.j$ext._w(cnt, flex_info.size);
						
						cnt.css({top: me.p_off_top, left: me.p_off_left});
						
						if (flex_info.cols < 2 || (flex_info.cols > 1 && (k + 1) % flex_info.cols == 0))
						{
							me.p_off_top += flex_info.size;
							me.p_off_left = flex_info.margin;
						}
						else
						{
							me.p_off_left += flex_info.size;
						}
					}
				}

				toption.chart = {
					renderTo: cnt[0],
					type: me.getSeriesType(rcs, serie, mcr),
					spacingBottom: spacingBottom,
					echart_theme: mcr.echart_theme
				};
				
				if (cchart.options3d)
				{
					toption.chart.options3d = cchart.options3d;
				}
				
				/*
				if (options3d)
				{
					toption.plotOptions.pie.depth = options3d.depth;
				}
				*/
				
				serie.avoidLabelOverlap = true;
				
				if (/(funnel|pyramid)/.test(rcs))
				{
					serie.type = "funnel";
					serie.gap = 2;
					serie.left = cgrid.left || 10;
					serie.right = cgrid.right || 10;
					for (i=0; i < serie.data.length; i++)
					{
						csv = me.getPieLbl(mcr.piedatalabel, {
							point: {
								name: serie.data[i].name,
								fval: serie.data[i].fval
							},
							percentage: 99.9
						});
					
						cs = me.measureTextSize(10, csv);
						mw = Math.max(cs.width + 10, mw);
					}
					
					mw = mw + mcr.pielabeldist;
					
					if (mw > owidth * 0.5)
					{
						mw = owidth * 0.5;
					}
					
					toption.chart.marginRight = mw;
					
					if (rcs == "funnel")
					{
						serie.neckWidth = "30%";
						serie.neckHeight = "25%";
					}
				}
				else
				{
					serie.left = cgrid.left || 10;
					serie.right = cgrid.right || 10;
					serie.top = cgrid.top || 10;
					serie.bottom = cgrid.bottom || 10;
				}

				serie.type = me.getSeriesType(rcs, serie, mcr);
				toption.series = [serie];
				me.updateSeries(serie, mcr, toption);
				delete toption["xAxis"];
				delete toption["yAxis"];
				// toption.legend.show = false;
				coptions.push(toption);
			});
		}

		if (cf.length)
		{
			for (i=0; i < coptions.length; i++)
			{
				coptions[i].__cf = cf;
			}
		}
		
		for (i=0; i < coptions.length; i++)
		{
			coption = coptions[i];
			
			if (coption.visualMap && coption.visualMap.length)
			{
				for (j=0; j < coption.visualMap.length; j++)
				{
					var vmap = coption.visualMap[j],
						s = coption.series[j],
						b = 1;
						
					if (s && s.data && isNaN(vmap.min) && isNaN(vmap.max))
					{
						$.each(s.data, function(k, dt) {
							if (!isNaN(dt.value))
							{
								vmap.min = b ? dt.value : Math.min(dt.value, vmap.min);
								vmap.max = b ? dt.value : Math.max(dt.value, vmap.max);
								b = 0; 
							}
						});
					}
				}
			}
		}
		
		me.__coptions = coptions;

		me.render(coptions, mresult);
			
		return coptions;
	}
});