IG$.cVis.cartesian = $s.extend(IG$.cVis._echart, {
	charttype: "cartesian",
	subtype: null,

	p_off: 0,
	p_off_top: 0,
	p_off_left: 0,
	
	initComponent: function() {
		var me = this;
		IG$.cVis.cartesian.superclass.initComponent.apply(me, arguments);
	},

	fmtlbl: function(lbl) {
		var me = this;

		if (lbl.indexOf("&") > -1)
		{
			if (!me.dspan)
			{
				me.dspan = $("<span></span>")
					.css({
						position: "absolute",
						top: -100
					})
					.appendTo(me.container);
			}
			
			me.dspan.html(lbl);
			lbl = me.dspan.text();
		}
		return lbl;
	},
	
	_create_option: function(mresult) {
		var me = this,
			mcr = me.chart_option,
			c_cset = mcr.colorset || mresult.c_cset,
			spacingBottom = 1,
			coption = {},
			cgrid,
			titleposition = mcr.titleposition || "",
			ctitle;
			
		if (IG$.__chartoption && IG$.__chartoption.chartcolors && IG$.__chartoption.chartcolors[c_cset])
		{
			coption.color =  IG$.__chartoption.chartcolors[c_cset];
		}
		
		mcr.paddingtop = mcr.paddingtop || 0;
		mcr.paddingbottom = mcr.paddingbottom || 0;
		mcr.paddingright = mcr.paddingright || 0;
		mcr.paddingleft = mcr.paddingleft || 0;
		
		coption.chart = {
			renderTo: me.container,
			reflow: false,
			alignTicks: true,
			borderWidth: 0,
			spacingBottom: spacingBottom,
			echart_theme: mcr.echart_theme
		};
		
		cgrid = coption.grid = {
			left: 10 + mcr.paddingleft,
			top: 15 + mcr.paddingtop,
			bottom: 15 + mcr.paddingbottom,
			right: 10 + mcr.paddingright,
			containLabel: true
		};
		
		if (mcr.showtitle && mcr.title)
		{
			ctitle = coption.title = {
				text: mcr.title,
				show: true,
						
				textStyle: {
					align: "center"
				}
			};
			
			if (titleposition.indexOf("BOTTOM_") > -1)
			{
				ctitle.bottom = 10;
				cgrid.bottom += 10;
			}
			else
			{
				cgrid.top += 20;
			}

			if (titleposition.indexOf("_LEFT") > -1)
			{
				ctitle.textAlign = "left";
				ctitle.textStyle.align = "left";
				ctitle.left = 20;
			}
			else if (titleposition.indexOf("_RIGHT") > -1)
			{
				ctitle.textAlign = "left";
				ctitle.right = 120;
				ctitle.textStyle.align = "right";
			}
			else
			{
				ctitle.textAlign = "center";
				ctitle.right = "50%";
				ctitle.left = "50%";
			}
		}
		else
		{
			ctitle = coption.title = {
				show: false
			};
		}
		
		return coption;
	},
	
	_draw_legend: function(coption) {
		var me = this,
			mcr = me.chart_option,
			ctitle = coption.title,
			cgrid = coption.grid;
			
		clegend = coption.legend = {
			type: "scroll"
		};

		if (mcr.showlegend == true)
		{
			clegend.show = true;
			legendposition = mcr.legendposition = mcr.legendposition || "BOTTOM_CENTER";

			if (legendposition.indexOf("BOTTOM_") > -1)
			{
				clegend.orient = "horizontal";
				if (ctitle.show && ctitle.bottom > 0)
				{
					clegend.bottom = ctitle.bottom + 30;
				}
				else
				{
					clegend.bottom = 0;
				}
				cgrid.bottom += 20;
				
				if (mcr.datazoom)
				{
					cgrid.bottom += 40;
					clegend.bottom += 40;
				}
			}
			else if (legendposition.indexOf("TOP_") > -1)
			{
				clegend.orient = "horizontal";
				clegend.top = 10;
				cgrid.top += 40;
			}
			else
			{
				clegend.orient = "vertical";
				
				if (legendposition.indexOf("_TOP") > -1)
				{
					clegend.top = 10;
				}
				else if (legendposition.indexOf("_BOTTOM") > -1)
				{
					clegend.bottom = 10;
				}
				else if (legendposition.indexOf("_CENTER") > -1)
				{
					clegend.top = h / 2;
				}
			}
			
			if (clegend.orient == "horizontal")
			{
				if (legendposition.indexOf("_RIGHT") > -1)
				{
					clegend.right = 20;
				}
				else if (legendposition.indexOf("_LEFT") > -1)
				{
					clegend.left = 20; 
				}
			}
			else
			{
				if (legendposition.indexOf("RIGHT_") > -1)
				{
					clegend.right = 20;
				}
				else if (legendposition.indexOf("LEFT_") > -1)
				{
					clegend.left = 20;
				}
			}
		}
		else
		{
			clegend.show = false;
			
			if (mcr.datazoom)
			{
				cgrid.bottom += 40;
			}
		}
		
		if (mcr.lgndfloating)
		{
			clegend.floating = true;
			clegend.borderWidth = 1;
			clegend.backgroundColor = "#FFFFFF";
		}	
	},
	
	_prep_series_data: function(mresult) {
		var me = this,
			i, j,
			data = mresult._tabledata,
			fr = mresult.rowfix,
			fc = mresult.colfix,
			hidden_columns = (me.hidden_columns = {}),
			isthumb = me.isthumb,
			mcr = me.chart_option,
			rcs = mcr.subtype,
			swapaxis = mcr.swapaxis,
			maxchartresult = mcr.maxchartresult,
			useformatvalue = mcr.useformatvalue,
			measures = me.sheetoption.model.measures,
			bsync = 0,
			bsync_l,
			bpolarchart = me.bpolarchart,
			vmin,
			vmax,
			series, spvalue,
			lblnames,
			xticks = (me.xticks = []),
			sep = me.getSep(),
			cfr, bdatas, _fc, n = 1;
			
		cfr = fr;
		bdatas = Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length));
		me._dlen = bdatas;
		
		if (!isthumb)
		{
			if (mresult.pagestart > 0 || mresult.rows > mresult.pageend)
			{
				me.sh = bdatas / mresult.rowcnt;
			}
			else if (maxchartresult > 0 && maxchartresult < mresult.rowcnt)
			{
				me.sh = maxchartresult / mresult.rowcnt;
			}
		}
		else
		{
			scrollX = Math.floor((me.scrollX * mresult.rowcnt) / me.thumbhwidth) - mresult.pagestart;
			cfr += scrollX;
			bdatas = Math.min(data.length, bdatas + scrollX);
		}
			
		spvalue = me.spvalue = [];
		series = me.series = [];
		lblnames = me.lblnames = {};
			
		if (me.subtype == "syncchart")
		{
			bsync_l = (measures && measures.length > 1) ? measures.length : -1;
			bsync = bsync_l > 1 ? 1 : 0;
		}
			
		var tc = (data && data.length > 0) ? data[0].length : 0;
		var tr = (data ? data.length : 0);
		
		if (mresult.hidden_columns)
		{
			for (i=0; i < mresult.hidden_columns.length; i++)
			{
				hidden_columns[mresult.hidden_columns[i]] = 1; 
			}
		}
			
		if (swapaxis)
		{
			for (i=fr; i < tr; i++)
			{
				for (j=0; j < fc; j++)
				{
					if (hidden_columns[j])
						continue;
						
					lbl_i = me.fmtlbl(data[i][j].text);
					lbl = (j==0) ? lbl_i : lbl + lbl_i;
				}
				var s = {
					name: lbl,
					data: []
				};
				if (bpolarchart)
				{
					s.data_map = [];
				}
				series.push(s);
				spvalue.push(0);
			}
		}
		else
		{
			var lbl_k, lbl, lbl_c, lbl_i;

			for (i=fc; i < tc; i++)
			{
				if (hidden_columns[i])
					continue;
					
				lbl_k = -1;
				lbl = "";
				
				if (measures && measures.length == 1)
				{
					lbl_k = 0;
				}
				
				if (bsync)
				{
					for (j=0; j < fr; j++)
					{
						lbl_c = data[j][i];
						if (lbl_c.position == 3)
						{
							lbl_k = lbl_c.index;
							lblnames[lbl_k] = me.fmtlbl(lbl_c.text);
							continue;
						}
						lbl_i = me.fmtlbl(lbl_c.text);
						lbl = (j==0) ? lbl_i : lbl + sep + lbl_i;
					}
				}
				else
				{
					for (j=0; j < fr; j++)
					{
						lbl_c = data[j][i];
						lbl_i = me.fmtlbl(lbl_c.text);
						if (lbl_c.position == 3)
						{
							lbl_k = lbl_c.index;
						}
						lbl = (j==0) ? lbl_i : lbl + sep + lbl_i;
					}
				}
				let s = {
					name: lbl || lblnames[lbl_k],
					meas_k: lbl_k,
					data: []
				};
				
				if (bpolarchart)
				{
					s.data_map = [];
				}
				series.push(s);
				spvalue.push(0);
			}
		}

		var m = Math.max((swapaxis == true ? tc : data.length), 20),
			bformula = false,
			iswaterfall = rcs == "waterfall" && mcr.stack == false,
			np,
			_com,
			ename, dname,
			dt;

		if (swapaxis == true)
		{
			for (i=fc; i < Math.min(tc, (maxchartresult > 0 ? maxchartresult : tc)); i++)
			{
				bformula = false;

				for (j=0; j < fr; j++)
				{
					tlbl = me.fmtlbl(data[j][i].text);
					ename = (j == 0) ? tlbl : ename + sep + tlbl;
					if (data[j][i].position == 4)
					{
						bformula = true;
						break;
					}
				}
				if (bformula == false || useformula == true)
				{
					ci = 0;
					for (j=fr; j < tr; j++)
					{
						var f = me.mvalue(data[j][i], useformatvalue);
						if (isNaN(f) == true)
						{
							series[ci].data.push(null);
						}
						else
						{
							if (isNaN(vmin))
							{
								vmin = f;
								vmax = f;
							}
							else
							{
								vmin = Math.min(vmin, f);
								vmax = Math.max(vmax, f);
							}
							if (bpolarchart)
							{
								series[ci].data.push(f);
								series[ci].data_map.push({
									name: ename,
									x: i - fc,
									value: f,
									fval: data[j][i].text
								});
							}
							else
							{
								np = series[ci].data.length;
								vl = (iswaterfall && np > 0) ? f - spvalue[ci] : f;
								series[ci].data.push({
									name: ename,
									x: i - fc,
									value: vl,
									fval: data[j][i].text
								});

								spvalue[ci] = f;
							}
						}
						ci++;
					}

					xticks.push(ename);
					n++;
				}
			}
		}
		else
		{
			_uc = (tc == fc+1) ? 1 : 0;
			
			for (i=cfr; i < bdatas; i++)
			{
				bformula = false;
				_c = null;
				
				for (j=0; j < fc; j++)
				{
					if (hidden_columns[j])
					{
						ename = (j == 0) ? "" : ename + sep + "";
						dname = (j == 0) ? data[i][j].text : dname + sep + data[i][j].text;
					}
					else
					{
						ename = (j == 0) ? data[i][j].text : ename + sep + data[i][j].text;
						dname = (j == 0) ? data[i][j].text : dname + sep + data[i][j].text;
						if (data[i][j].position == 4)
						{
							bformula = true;
							break;
						}
					
						_c = _uc && data[i][j].cc ? data[i][j].cc : _c;
					}
				}
				
				if (bformula == false || useformula == true)
				{
					let ci = 0;
					for (j=fc; j < tc; j++)
					{
						if (hidden_columns[j])
							continue;
						
						f = me.mvalue(data[i][j], useformatvalue);
						
						if (isNaN(f))
						{
							if (bpolarchart)
							{
								series[ci].data.push(0);
								series[ci].data_map.push({
									name: ename,
									drname: dname,
									x: i - cfr,
									y: null,
									fval: null
								});
							}
							else
							{
								dt = {
									name: ename,
									drname: dname,
									x: i - cfr,
									y: null,
									fval: null
								};
								data[i][j].selected && me._highlight(dt);
								series[ci].data.push(dt);
							}
						}
						else
						{
							if (isNaN(vmin))
							{
								vmin = f;
								vmax = f;
							}
							else
							{
								vmin = Math.min(vmin, f);
								vmax = Math.max(vmax, f);
							}
							
							if (bpolarchart)
							{
								series[ci].data.push(f);
								series[ci].data_map.push({
									name: ename,
									drname: dname,
									x: i - cfr,
									value: f,
									fval: data[i][j].text
								});
							}
							else
							{
								np = series[ci].data.length;
								vl = (iswaterfall && np > 0) ? f - spvalue[ci] : f;
								_com = {
									name: ename,
									drname: dname,
									x: i - cfr,
									value: vl,
									fval: data[i][j].text
								};
								
								if (!_c && data[i][j].cm > -1)
								{
									var colors = coption.colors;
									if (colors)
									{
										_fc = me.interpolate(data[i][j].cm, colors[ci % colors.length]);
									}
								}
								
								if (_c || _fc)
								{
									_com.color = _c || _fc;
								}
								
								_fc = 0;
								data[i][j].selected && me._highlight(_com);
								series[ci].data.push(_com);

								spvalue[ci] = f;
							}
						}
						ci++;
					}

					xticks.push(ename);
					n++;
				}
			}
		}
		return series;
	},
	
	_createAxis: function(coption) {
		var me = this,
			xticks = me.xticks,
			mcr = me.chart_option;
			
		var xlabels = {
			axisLabel: {
				maxStaggerLines: 2,
				interval: "auto",
				hideOverlap: mcr.xaxishideoverlap,
				overflow: "breakAll",
				formatter: function(value, index) {
					var r = value,
						m,
						paramsNameNumber,
						rowNumber,
						xaxislbllen = mcr.xaxislbllen,
						p,
						newParamsName;
						
					if (mcr.xaxismline)
					{
						r = r.split(" ").join("\n");
					}
					
					if (mcr.xaxislbllen > 1)
					{
						paramsNameNumber = r.length;// Number of actual labels
                        rowNumber = Math.ceil(paramsNameNumber / xaxislbllen);// If you change lines, you need to show a few lines and take them up.
                        /**
                         * Determine whether the number of tags is greater than the specified number, and if it is greater than the number of new lines, if it is not greater than, that is, equal to or less than, return to the original tag.
                         */
                        // The condition is equal to rowNumber > 1
                        if (paramsNameNumber > xaxislbllen) {
                            /** Loop each row, p represents the row */
                            newParamsName = "";
                            for (p = 0; p < rowNumber; p++) {
                                var tempStr = "";// A string representing each intercept
                                var start = p * xaxislbllen;// Starting interception position
                                var end = start + xaxislbllen;// End Interception Position
                                // The index value of the last row is specially handled here
                                if (p == rowNumber - 1) {
                                    // No change of line for the last time
                                    tempStr = r.substring(start, paramsNameNumber);
                                } else {
                                    // Each concatenation of strings and line breaks
                                    tempStr = r.substring(start, end) + "\n";
                                }
                                newParamsName += tempStr;// The final string
                            }
							
							r = newParamsName;
                        }						}
					else if (mcr.xstagl > 1)
					{
						m = index % mcr.xstagl;
						
						if (m > 0)
							r = "\n".repeat(m) + r;
					}
					
					return r;
				}
			}
		};
		
		if (mcr.xlabel === false)
		{
			xlabels.axisLabel.enabled = false;
		}
		else
		{
			if (mcr.xaxisrot == true)
			{
				xlabels.axisLabel.rotate = mcr.xaxisrotang || 0;
			}
			else
			{
				xlabels.axisLabel.autoRotation = false;
				
				if (mcr.xstagl > 0)
				{
					xlabels.axisLabel.staggerLines = mcr.xstagl;
					xlabels.axisLabel.maxStaggerLines = mcr.xstagl;
				}
				
				if (mcr.xstep > 0)
				{
					xlabels.axisLabel.interval = mcr.xstep;
				}
			}
		}
		
		coption.xAxis = {
			data: xticks,
			axisLabel: xlabels.axisLabel,
			showLastTickLabel: true,
			align: "left",
			events: {
				setExtremes: function (event) {
					if (Math.abs(this.options.labels.rotation) == 90)
					{
						var labelWidth = parseInt(this.options.labels.style.lineHeight) + 2,
							plotAreaWidth = parseInt(this.chart.plotBox.width),
							labelsCount = Math.floor(plotAreaWidth / labelWidth),
							pointsCount,
							step;

						if (event.max !== null && event.max !== undefined)
						{
							pointsCount = Math.round(event.max - event.min);
						}
						else
						{
							pointsCount = Math.round(this.dataMax - this.dataMin);
						}

						step = Math.ceil(pointsCount / (labelsCount * (this.tickInterval == null ? 1 : this.tickInterval)));

						this.update({
							labels: {
								step: step
							}
						}, true);
					}
				}
			}
		};
		
		if (mcr.xlabel === false)
		{
			coption.xAxis.show = false;
		}
	},

	_createYAxis: function(coption) {
		var me = this,
			mseq,
			cyAxis,
			cgrid = coption.grid,
			mcr = me.chart_option,
			bpolarchart = me.bpolarchart,
			ylabels = {
				formatter: mcr.yaxisformat ? function(value, index) {
					var yaxisformat = mcr.yaxisformat;
					return IG$.NumberFormat(yaxisformat, value);
				} : null
			};
			
		cyAxis = coption.yAxis = {
			name: mcr.yaxistitle || null, 
			axisLabel: {
				formatter: ylabels.formatter
			}
		};
		
		if (cyAxis.name)
		{
			cyAxis.nameLocation = "end";
			cgrid.top += 20;
		}

		if (mcr.yaxismax != "" && IG$.isNumber(mcr.yaxismax) == true)
		{
			cyAxis.max = Number(mcr.yaxismax);
		}

		if (mcr.yaxismin != "" && IG$.isNumber(mcr.yaxismin) == true)
		{
			cyAxis.min = Number(mcr.yaxismin);
		}
		
		if (mcr.ytickint > 0)
		{
			cyAxis.interval = mcr.ytickint;
		}
		
		if (mcr.yaxislog)
		{
			cyAxis.type = "log";
			
			cyAxis.minorTick = {
				show: true
			};
			
			if (mcr.yaxislogbase > 0)
			{
				cyAxis.logBase = mcr.yaxislogbase;
			}
		}
		
		if (mcr.yaxisline)
		{
			cyAxis.splitLine = cyAxis.splitLine || {};
			cyAxis.splitLine.show = false;
		}

		if (bpolarchart)
		{
			cyAxis.gridLineInterpolation = "polygon";
			cyAxis.lineWidth = 0;
		}
	},
	
	_enable_drag_select: function(coption) {
		coption.toolbox = {
			feature: {
				brush: {
					title: {
						rect: IRm$.r1("L_CHART_SEL_RECT"),
						// polygon: IRm$.r1("L_CHART_SEL_POLY"),
						// lineX: IRm$.r1("L_CHART_SEL_HOR"),
						// lineY: IRm$.r1("L_CHART_SEL_VER"),
						// keep: IRm$.r1("L_CHART_SEL_KEEP"),
						clear: IRm$.r1("L_CHART_SEL_CLEAR")
					}
				}
			}	
		};
		
		coption.brush = {
			toolbox: [
				"rect", 
				"keep", 
				"clear"
			],
			brushMode: "multiple"
		};
	},
	
	_enable_zoom: function(coption) {
		coption.toolbox = {
			feature: {
				dataZoom: {
					show: true,
					title: {
						zoom: IRm$.r1("L_CHART_Z_A"),
						back: IRm$.r1("L_CHART_Z_B")
					}
				}
			}
		};
	},
	
	_enable_3d: function(coption) {
		var me = this,
			mcr = me.chart_option,
			cchart = coption.chart;

		cchart.options3d = {
			enabled: true,
			alpha: Number(mcr.e3d_al),
			beta: Number(mcr.e3d_be),
			depth: Number(mcr.e3d_de),
			viewDistance: Number(mcr.e3d_vd)
		};
	},
	
	getPieLbl: function(piedatalabel, series) {
		var me = this,
			fmt = piedatalabel || "{name}: {percent} %",
			n,
			pct,
			mcr = me.sheetoption.model.chart_option,
			piepctprec,
			pcval;
			
		n = fmt.indexOf("{name}");
		if (n > -1)
		{
			fmt = fmt.substring(0, n) + series.name + fmt.substring(n + "{name}".length);
		}
		n = fmt.indexOf("{percent}");
		if (n > -1)
		{
			pct = series.percentage || series.percent;
			
			if (mcr)
			{
				piepctprec = mcr.piepctprec;
				
				if (typeof(piepctprec) != "undefined" && piepctprec > -1 && piepctprec < 3)
				{
					pcval = pct * Math.pow(10, piepctprec);
					pcval = Math.round(pcval);
					pcval = pcval / Math.pow(10, piepctprec);
					
					pct = pcval.toFixed(piepctprec);
				}
			}
			
			fmt = fmt.substring(0, n) + pct + fmt.substring(n + "{percent}".length);
		}
		n = fmt.indexOf("{value}");
		if (n > -1)
		{
			if (series.data)
			{
				fmt = fmt.substring(0, n) + (series.data.fval || series.data.value) + fmt.substring(n + "{value}".length);
			}
			else if (series.point)
			{
				fmt = fmt.substring(0, n) + (series.point.fval || series.point.value) + fmt.substring(n + "{value}".length);
			}
			else
			{
				fmt = fmt.substring(0, n) + fmt.substring(n + "{value}".length);
			}
		}
		n = fmt.indexOf("<br>");
		while (n > -1)
		{
			fmt = fmt.substring(0, n) + "\n" + fmt.substring(n + "<br>".length);
			n = fmt.indexOf("<br>");
		}
		return fmt;
	},
	
	updateSeries: function(series, mcr, coption) {
		var me = this;
		
		if (series.type == "scatter")
		{
			series.symbol = "circle";
			
			if (mcr.charttype == "bubble")
			{
				series.symbolSize = function(data) {
					return me.__bubblemax - me.__bubblemin > 0 ? Math.sqrt((data[2] - me.__bubblemin) / (me.__bubblemax - me.__bubblemin)) * 80 + 10 : Math.sqrt(data[2]);
				}
			}
			else
			{
				series.symbolSize = 10;
			}
		}
		else if (/(line|bar|radar)/.test(series.type))
		{
			series.symbol = mcr.dl_marker_type || "circle";
			if (!series.symbolSize)
			{
				series.symbolSize = (mcr.dl_marker ? (mcr.dl_marker_size || 6) : 2);
			}
			series.showSymbol = true;
		}
		else if (/(area)/.test(series.type))
		{
			series.symbol = mcr.dl_marker ? (mcr.dl_marker_type || "circle") : "none";
			series.symbolSize = mcr.dl_marker_size || 2;
			series.showSymbol = true;
		}
		else if (/(funnel|pyramid)/.test(series.type))
		{
			series.label = {
				show: true,
				softConnector: true,
				position: "inside",
				distance: mcr.pielabeldist,
				formatter: function (s) {
					return me.getPieLbl(mcr.piedatalabel, s);
				}
			};
		}
		else if (series.type == "pie")
		{
			var pinner = mcr.subtype == "doughnut" ? (mcr.pieinnerradius > 20 ? mcr.pieinnerradius : 40) : (mcr.pieinnerradius || 0),
				pouter = (mcr.pieouterradius || 90);
				
			$s.apply(series, {
				allowPointSelect: true,
				avoidLabelOverlap: true,
				itemStyle: {
					borderRadius: 5,
					borderColor: '#fff',
					borderWidth: 1
				},
				cursor: "pointer",
				radius: [("" + (pinner) + "%"), pouter + "%"],
				emphasis: {
					label: {
						show: true,
						fontSize: mcr.dl_position == "center" ? 25 : 14,
						fontWeight: 'bold'
					}
				},
				label: {
					show: mcr.dl_position == "center" ? false : mcr.dl_enable,
					bleedMargin: 10,
					distance: mcr.pielabeldist,
					// position: mcr.dl_inside ? "inside" : "outside",
					position: mcr.dl_position == "edge" ? "outside" : mcr.dl_position,
					// alignTo: "edge",
					// formatter: me.getPieFmt(mcr.piedatalabel)
					
					formatter: function (s) {
					 	return me.getPieLbl(mcr.piedatalabel, s);
					}
					
				},
				labelLine: {
					show: mcr.dl_position == "center" ? false : true
				},
				events: {
					click: function(param) {
						eventowner.procClickEvent.call(eventowner, this, param);
					}
				}
			});

			if (mcr.dl_position == "edge")
			{
				$s.apply(series.label, {
					alignTo: "edge",
					edgeDistance: 10,
					minMargin: 5,
					lineHeight: 15
				})

				$s.apply(series, {
					labelLine: {
						length: 15,
						length2: 0,
						maxSurfaceAngle: 80
					},
					labelLayout: function (params) {
						const isLeft = params.labelRect.x < $(coption.chart.renderTo).width() / 2;
						const points = params.labelLinePoints;
						if (!points)
							return;
						// Update the end point.
						points[2][0] = isLeft ? params.labelRect.x : params.labelRect.x + params.labelRect.width;
						return {
							labelLinePoints: points
						};
					}
				})
			}
			
			if (mcr.dl_enable && mcr.dl_position == "center")
			{
				series.emphasis = {
					label: {
						show: true,
						fontSize: "18",
						fontWeight: "bold"
					}
				}
			}
		}

		mcr.settings = mcr.settings || {};

		if (mcr.settings.de_enable && /(line|area)/.test(series.type))
		{
			var de_format = mcr.settings.de_format || "{name} : {value}";
			series.endLabel = {
				show: true,
				formatter: function(params) {
					return de_format
						.replace("{value}", params.data.fval)
						.replace("{name}", params.seriesName);
				}
			}

			series.emphasis = series.emphasis || {};
			series.emphasis.focus = "series";
			series.labelLayout = series.labelLayout || {};
			series.labelLayout.moveOverlap = "shiftY";

			coption.tooltip = coption.tooltip || {};
			coption.tooltip.order = "valueDesc";
			coption.tooltip.trigger = "axis";
		}
		
		series.large = true;
		series.largeThreshold = mcr.maxchartresult || 1000;
		
		series.events = {
			click: function(param) {
				eventowner.procClickEvent.call(eventowner, this, param);
				return false;
			}
		};
		
		if (mcr.dl_enable && series.type != "pie")
		{
			series.label = series.label || {};
			
			if (series.label.show !== false)
			{
				series.label.show = true;
				series.label.align = series.label.align || mcr.dl_align;
				series.label.formatter = function(params) {
					if (IG$.chartLabelOverride)
					{
						return IG$.chartLabelOverride.apply(this, [params]);
					}
					return params.data.fval;
				};
				
				var l_pos = series.label.position || mcr.dl_position;
				
				if (series.type == "bar" && mcr.subtype == "bar")
				{
					switch(l_pos)
					{
					case "top":
						l_pos = "right";
						series.label.align = "left";
						break;
					case "bottom":
					case "insideBottom":
						l_pos = "insideLeft";
						series.label.align = "left";
						break;
					case "insideTop":
						l_pos = "insideRight";
						series.label.align = "right";
						break;
					case "inside":
						break;
					}
				}
				
				if (l_pos)
				{
					series.label.position = l_pos;
				}
			}
		}
		
		if (mcr.stack == true && me.rcs != "waterfall")
		{
			series.stack = "stack_001";
		}
	},

	_before_draw: function(mresult) {
		var me = this;

		me.p_off = 0;
		me.p_off_top =0;
		me.p_off_left = 0;
	},
	
	draw: function(mresult) {
		var me = this,
			coptions = IG$.cVis.cartesian.superclass.draw.apply(me, arguments),
			i,
			j,
			data = mresult._tabledata,
			fc = mresult.colfix,
			fr = mresult.rowfix,
			hasdualaxis,
			n = 1,
			ename,
			dname,
			series = [],
			ci,
			labels = [],
			bpointchart = 0,
			bpolarchart = 0,
			bscatter = 0,
			bfixedp = 0,
			bsync = 0,
			bsync_l = -1,
			bdatas,
			cfr,
			scrollX,
			
			mcr = me.chart_option,
			rcs = mcr.subtype,
			maxchartresult = mcr.maxchartresult,
			measures = me.sheetoption.model.measures,
			f, _c, _uc, _fc,
			lbl, lbl_i, lbl_c, lbl_k, lblnames = {},
			mtitle,
			cf = [],
			sep = mcr.xaxissep || ig$.chart_separator || IG$._separator,
			temp,
			// line, spline, area, areaspline, column, bar, pie and scatter
			spvalue = [],
			npos;
		
		sep = sep == "nl" ? "\n" : sep;

		me._before_draw(mresult);
		
		if (rcs == "radar")
		{
			bpolarchart = 1;
		}
		else if (rcs == "fixedplacement")
		{
			bfixedp = 1;
		}
		else if (rcs == "syncchart")
		{
			bsync_l = (measures && measures.length > 1) ? measures.length : -1;
			bsync = bsync_l > 1 ? 1 : 0;
		}
		
		bdatas = Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length));
		me._dlen = bdatas;
		
		var coption,
			dl_tooltip = mcr.dl_tooltip,
			spacingBottom = 1,
			hidden_columns,
			vl,
			vmin,
			vmax,
			tlbl;
		
		coption = me._create_option(mresult);

		series = me._prep_series_data(mresult);
		lblnames = me.lblnames;
		
		hidden_columns = me.hidden_columns;
		
		var cgrid,
			cchart,
			ctitle,
			cyAxis;
		
		cchart = coption.chart;
		cgrid = coption.grid;
		ctitle = coption.title;

		spacingBottom = cchart.spacingBottom;
		
		if (mcr.enabledragselect)
		{
			me._enable_drag_select(coption);
		}
		else if (mcr.enablezoom)
		{
			me._enable_zoom(coption);
		} 
			
		me._createAxis(coption);
			
		me._createYAxis(coption);
		cyAxis = coption.yAxis;

		me._draw_legend(coption);
		
		var options3d = cchart ? cchart.options3d : null;
			
		clegend.data = [];
		
		if (coption.title && coption.title.text)
		{
			mtitle = coption.title.text;
			mtitle = IG$.replaceAll(mtitle, "{SERIES}", "");
			coption.title.text = mtitle;
		}
		
		var bgrid = 1,
			mseq;
		
		for (i=0; i < series.length; i++)
		{
			if (!bfixedp && !bpolarchart)
			{
				mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
				
				if (mseq > -1 && mcr.renderas && mcr.renderas.length > mseq)
				{
					if (mcr.renderas[mseq])
					{
						series[i].type = me.getSeriesType(mcr.renderas[mseq], series[i], mcr);
					}
					
					if (bgrid && series[i].type == "custom" && mcr.custxlabeltype == "vertical")
					{
						bgrid = 0;
						coption.grid.bottom += 30;
					}
					
					if (mcr.dl_linetype_l && mcr.dl_linetype_l[mseq])
					{
						series[i].lineStyle = series[i].lineStyle || {};
						if (mcr.dl_linetype_l[mseq] == "hide")
						{
							series[i].lineStyle.opacity = 0;
							series[i].showAllSymbol = true;
						}
						else
						{
							series[i].lineStyle.type = mcr.dl_linetype_l[mseq];
						}
					}
					
					if (mcr.dl_linewidth_l && mcr.dl_linewidth_l[mseq])
					{
						series[i].lineStyle = series[i].lineStyle || {};
						series[i].lineStyle.width = Number(mcr.dl_linewidth_l[mseq] || "1") || 1;
					}
					
					if (mcr.dl_position_l && mcr.dl_position_l[mseq])
					{
						series[i].label = series[i].label || {
							formatter: function(params) {
								if (IG$.chartLabelOverride)
								{
									return IG$.chartLabelOverride.apply(this, [params]);
								}
								return params.data.fval;
							}
						};
						series[i].label.position = mcr.dl_position_l[mseq];
					}
					
					if (mcr.dl_c_range_l && mcr.dl_c_range_l[mseq])
					{
						var c_range = mcr.dl_c_range_l[mseq],
							vmap;
						
						if (c_range.mincolor && (c_range.maxcolor || c_range.midcolor))
						{
							coption.visualMap = coption.visualMap || [];
							
							vmap = {
								type: "continuous",
								orient: "horizontal",
								right: 20,
								top: 5,
								show: coption.dl_c_hidden ? false : true,
								seriesIndex: i,
								// dimension: 0,
								// seriesIndex: [i],
								color: []
							};
							
							c_range.maxcolor && vmap.color.push(c_range.maxcolor);
							c_range.midcolor && vmap.color.push(c_range.midcolor);
							vmap.color.push(c_range.mincolor);
							
							if (c_range.dl_c_min && c_range.dl_c_max)
							{
								vmap.min = Number(c_range.dl_c_min);
								vmap.max = Number(c_range.dl_c_max);
							}
							
							coption.visualMap.push(vmap);
						}
					}
				}
			}
			
			if (mcr.dl_enable_l && mcr.dl_enable_l.length > mseq)
			{
				if (mcr.dl_enable_l[mseq] == "F")
				{
					series[i].label = series[i].label || {};
					series[i].label.show = false;
				}
				else if (mcr.dl_enable_l[mseq] == "T")
				{
					series[i].label = series[i].label || {};
					series[i].label.show = true;
					series[i].label.position = mcr.dl_position_l && mcr.dl_position_l[mseq] ? mcr.dl_position_l[mseq] : null;
				}
			}
			
			if (mcr.dl_marker_l && mcr.dl_marker_l.length > mseq)
			{
				if (mcr.dl_marker_l[mseq] == "F")
				{
					series[i].symbolSize = 2;
				}
				else if (mcr.dl_marker_l[mseq] == "T")
				{
					series[i].symbol = "circle";
					series[i].symbolSize = 6;
					series[i].showSymbol = true;
				}
			}
			
			if (series[i].type != "custom")
			{
				clegend.data.push({
					name: series[i].name
				});
			}
		}

		if (mcr.usedualaxis == true && mcr.dualaxisitem && mcr.dualaxisitem.length > -1 && series.length > 1)
		{
			for (i=0; i < series.length; i++)
			{
				mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
				
				if (mcr.dualaxisitem.length > mseq && mcr.dualaxisitem[mseq] == "T")
				{
					hasdualaxis = true;
					break;
				}
			}

			if (hasdualaxis == true)
			{
				var dyax = {
					name: mcr.yaxistitle2 ? mcr.yaxistitle2 : null,
					position: "right",
					axisLabel: {
						formatter: mcr.yaxisformat2 ? function(value, index) {
							var yaxisformat = mcr.yaxisformat2;
							return IG$.NumberFormat(yaxisformat, value);
						} : null
					}
				};
				
				if (mcr.yaxishide2)
				{
					dyax.show = false;
				}
				
				if (dyax.name)
				{
					dyax.nameLocation = "end";
					if (!mcr.yaxistitle)
					{
						cgrid.top += 20;
					}
				}

				if (mcr.yaxismax2 != "" && IG$.isNumber(mcr.yaxismax2) == true)
				{
					dyax.max = Number(mcr.yaxismax2);
				}

				if (mcr.yaxismin2 != "" && IG$.isNumber(mcr.yaxismin2) == true)
				{
					dyax.min = Number(mcr.yaxismin2);
				}
				
				if (mcr.yaxislog2)
				{
					dyax.type = "log";
					
					if (mcr.yaxislogbase2 > 0)
					{
						dyax.logBase = mcr.yaxislogbase2;
					}
				}
				
				if (mcr.yaxisline2)
				{
					dyax.splitLine = dyax.splitLine || {};
					dyax.splitLine.show = false;
				}
				
				coption.yAxis = [coption.yAxis];
				coption.yAxis.push(dyax);
				
				for (i=0; i < series.length; i++)
				{
					mseq = (mcr.renderbymeasures ? series[i].meas_k : i);
					
					if (mcr.dualaxisitem.length > mseq && mcr.dualaxisitem[mseq] == "T")
					{
						if (rcs == "bar")
						{
							series[i].xAxisIndex = 1;
						}
						else
						{
							series[i].yAxisIndex = 1;
						}
					}
				}
			}
		}

		// if ((rcs == "line" || rcs == "spline") && series.length && series[0].data && series[0].data.length)
		// {
		// 	// Make gradient line here
		// 	coption.visualMap = [
		// 	  {
		// 		show: false,
		// 		type: 'continuous',
		// 		seriesIndex: 0,
		// 		min: 0,
		// 		max: 400
		// 	  },
		// 	  {
		// 		show: false,
		// 		type: 'continuous',
		// 		seriesIndex: 1,
		// 		dimension: 0,
		// 		min: 0,
		// 		max: series[0].data.length - 1
		// 	  }
		// 	];
		// }
		
		if (dl_tooltip)
		{
			var ctooltip = coption.tooltip = {
				formatter: function(params, ticket, callback) {
					var r,
						i,
						value = params.value,
						rec;
					
					if (bfixedp)
					{
						if (this.points && this.points.length)
						{
							r = this.points[0].x + "<br>";
							for (i=0; i < this.points.length; i++)
							{
								r += (i > 0 ? "<br>" : "") + "<b>" + this.points[i].series.name + "</b> " + (this.points[i].fval || IG$.NumberFormat("#,###", this.points[i].y));
							}
						}
					}
					else if (bpointchart)
					{
						if (value.length == 4)
						{
							rec = data[value[3]];
							r = params.seriesName + "<br>" 
								+ (params.name || value[2]) + "<br>" 
								+ "X: " + (rec[fc].text || "") 
								+ (rec[fc+1] ? "<br>Y: " + (rec[fc+1].text || "") : "");
						}
						else if (value.length == 5)
						{
							rec = data[value[4]];
							r = params.seriesName + "<br>" 
							+ (params.name || value[3]) + "<br>" 
							+ "X: " + (rec[fc].text || "") 
							+ (rec[fc+1] ? "<br>Y: " + (rec[fc+1].text || "") : "")
							+ (rec[fc+2] ? "<br>Z: " + (rec[fc+2].text || "") : "");
						}
					}
					else if (bsync)
					{
						if (params && params.length)
						{
							r = [];
							$.each(params, function(i, param) {
								var sr = (param.seriesName || "") + " - " 
									+ param.name + " : "
									+ (param.data.fval || param.value);
								
								r.push(sr);
							});
							
							r = r.join("<br>");
						}
					}
					else if (bpolarchart)
					{
						r = [
							params.name
						];
						
						if (params.data && params.data.value)
						{
							for (i=0; i < params.data.value.length; i++)
							{
								r.push("<br/>");
								if (params.data.data_map && params.data.data_map[i])
								{
									r.push("<b>" + coption.radar.indicator[i].name + "</b>: " + params.data.data_map[i].fval);
								}
								else
								{
									r.push("<b>" + coption.radar.indicator[i].name + "</b>: " + params.data.value[i]);
								}
							}
						}
						
						r = r.join("");
					}
					else
					{
						if (IG$.chartTooltipOverride)
						{
							r = IG$.chartTooltipOverride.apply(this, [params]);
						}
						else
						{
							if (params.length && params.push)
							{
								r = ["<span class='igc_ps_2'>" + params[0].name + "</span>" ];
								for (i=0; i < params.length; i++)
								{
									var p = params[i];
									var s = (p.seriesName ? "<span class='igc_ps_1'>" + p.seriesName + "</span> : " : "") 
										+ "<span class='igc_ps_value'>" + (p.data.fval || p.value) + "</span>";

									r.push(s);
								}
								r = r.join("<br/>\n");
							}
							else
							{
								r = (params.seriesName ? params.seriesName + "<br>" : "") 
									+ params.name + "<br>" 
									+ (params.data.fval || params.value);
							}
						}
					}
					return r;
				}
			};
		}
				
		if (mcr.bands && mcr.bands.length)
		{
			var s0 = series[0];
			
			$.each(mcr.bands, function(i, bp) {
				var pb;

				if (bp.enabled)
				{
					if (bp.type == "curvefit")
					{
						cf.push(bp);
					}
					else
					{
						if (bp.type == "band")
						{
							bp.from = me.getBandValue(mresult, bp.value_1, bp.isxaxis);
							bp.to = me.getBandValue(mresult, bp.value_2, bp.isxaxis);
							
							var ts = s0,
								k = 0;
							
							while (ts && ts.markArea)
							{
								ts = series[++k];
							}
							
							if (!ts)
								return;
							
							var m = {data: []};
							ts.markArea = m;
							
							m.name = bp.name;
							m.label = {
								show: true,
								formatter: function() {
									return bp.name;
								},
								position: "insideTopRight"
							};
							
							m.tooltip = {
								formatter: bp.name + "<br /> {c0} ~ {c1} "
							};
							
							if (bp.isxaxis)
							{
								m.data.push([
									{
										name: "start",
										xAxis: bp.from.value,
										itemStyle: {
											color: bp.color,
											borderWidth: Number(bp.borderwidth),
											borderType: "solid",
											opacity: 0.4
										}
									},
									{
										name: "end",
										xAxis: bp.to.value,
										itemStyle: {
											color: bp.color,
											borderWidth: Number(bp.borderwidth),
											borderType: "solid",
											opacity: 0.4
										}
									}
								]);
								
								m.tooltip.formatter = bp.name + "<br /> " + (bp.from.code || bp.from.value.toFixed(0)) + " ~ " + (bp.to.code || bp.to.value.toFixed(0));
							}
							else
							{
								m.data.push([
									{
										name: "start",
										yAxis: bp.from.value,
										value: bp.from.value,
										itemStyle: {
											color: bp.color,
											borderWidth: Number(bp.borderwidth),
											borderType: me.getLineType(bp.cf_lc),
											opacity: 0.4
										}
									},
									{
										name: "end",
										yAxis: bp.to.value,
										value: bp.to.value,
										itemStyle: {
											color: bp.color,
											borderWidth: Number(bp.borderwidth),
											borderType: me.getLineType(bp.cf_lc),
											opacity: 0.4
										}
									}
								]);
								
								m.tooltip.formatter = bp.name + "<br /> " + bp.from.value.toFixed(0) + " ~ " + bp.to.value.toFixed(0);
							}
							
						}
						else if (bp.type == "line")
						{
							var bv =me.getBandValue(mresult, bp.value_1, bp.isxaxis);
							bp.value = bv.value;
							
							s0.markLine = s0.markLine || {symbol: "none", data: []};
							
							if (bp.isxaxis)
							{
								s0.markLine.data.push({
									name: bp.name,
									xAxis: bp.value,
									symbol: "none",
									label: {
										position: "insideEndTop",
										formatter: "{b}"
									},
									lineStyle: {
										type: me.getLineType(bp.cf_lc),
										color: bp.color,
										width: Number(bp.borderwidth) || 2,
										opacity: 0.4
									}
								});
							}
							else
							{
								s0.markLine.data.push({
									name: bp.name,
									yAxis: bp.value,
									symbol: "none",
									label: {
										position: "insideEndTop",
										formatter: "{b}"
									},
									lineStyle: {
										type: me.getLineType(bp.cf_lc),
										color: bp.color,
										width: Number(bp.borderwidth) || 2,
										opacity: 0.4
									}
								});
							}
						}
					}
				}
			});
		}

		if (mcr.stack == true && rcs != "waterfall")
		{
			for (i=0; i < series.length; i++)
			{
				series[i].stack = "stack001";
			}
			
			if (mcr.stackperc && series.length)
			{
				for (i=0; i < series[0].data.length; i++)
				{
					var tval,
						dval;
					
					for (j=0; j < series.length; j++)
					{
						dval = series[j].data[i].value || 0;
						tval = (j == 0) ? dval : dval + tval;
					}
					
					if (tval)
					{
						for (j=0; j < series.length; j++)
						{
							dval = series[j].data[i].value || 0;
							series[j].data[i].value = dval / tval * 100;
						}
					}
				}
			}
		}

		if (rcs == "radar")
		{
			temp = coption.xAxis;
			coption.radar = {
				name: {
					textStyle: {
				
						color: "#fff",
						backgroundColor: "#999",
						borderRadius: 3,
						padding: [3, 5]
					}
				},
				axisTick: {
					show: true
				},
				indicator: [],
				legendselectchanged: function(mchart, param) {
					var selected = param.selected,
						smax,
						smin,
						sval,
						i, j,
						sdata,
						toptions,
						indicator;
					
					if (coption.series.length && coption.series[0].data)
					{
						sdata = coption.series[0].data;
						for (i=0; i < sdata.length; i++)
						{
							if (selected[sdata[i].name])
							{
								for (j=0; j < sdata[i].value.length; j++)
								{
									sval = sdata[i].value[j];
									
									if (isNaN(sval) == false)
									{
										if (isNaN(smin))
										{
											smin = sval;
											smax = sval;
										}
										else
										{
											smin = Math.min(smin, sval);
											smax = Math.max(smax, sval);
										}
									}
								}
							}
						}
						
						toptions = mchart.getOption();
						indicator = toptions.radar[0].indicator;
						for (i=0; i < indicator.length; i++)
						{
							indicator[i].min = smin;
							indicator[i].max = smax;
						}
						
						mchart.setOption(toptions);
					}
				}
			};
			
			coption.yAxis.gridLineInterpolation = "polygon";
			coption.yAxis.lineWidth = 0;
			
			for (i=0; i < temp.data.length; i++)
			{
				coption.radar.indicator.push({
					name: temp.data[i]
				});
			}
			
			delete coption["xAxis"];
			delete coption["yAxis"];
		}
		else
		{
			if (rcs == "bar")
			{
				cgrid.right += 20;
				temp = coption["xAxis"];
				coption.xAxis = coption.yAxis;
				delete coption["yAxis"];
				coption.yAxis = temp;
				temp.inverse = true;
				
				if (coption.xAxis.length && coption.xAxis.length > 1)
				{
					coption.xAxis[1].position = "top";
				}
			}
		
			// multiple axis support on cartesian chart
			if (fc > 1 && rcs != "bubble" && rcs != "scatter" && mcr.xlabel !== false && mcr.xgroupcategory !== false)
			{
				var is_bar = rcs == "bar",
					nxaxis = [],
					cxaxis,
					pxaxis = is_bar ? coption.yAxis : coption.xAxis,
					aval, sval, t, ppval,
					pos = is_bar ? "left" : "bottom",
					lc, pl = 0,
					ppseq = -1,
					ppseqval,
					seq,
					ccval;
				
				coption.grid = coption.grid || {};
				
				if (is_bar)
				{
					coption.grid.left = coption.grid.left - 10;
				}
				else
				{
					coption.grid.bottom = coption.grid.bottom - 10;
				}
				
				if (fc > 0 && hidden_columns[fc - 1])
				{
					pxaxis.splitLine = pxaxis.splitLine || {}; 
					pxaxis.splitLine.show = false;
					pxaxis.axisTick = pxaxis.axisTick || {};
					pxaxis.axisTick.show = false;
				}
				
				nxaxis.push(pxaxis);
				
				pxaxis.axisLabel.formatter = function(value, index) {
					var nval = value ? value.split(sep) : null;
					return nval && nval.length ? nval[nval.length-1] : "";
				};
				
				for (i=fc-2; i >=0; i--)
				{
					seq = fc - 2 - i;
					
					if (hidden_columns[i])
						continue;
					
					cxaxis = {
						position: pos,
						// offset: 20 * (seq+1),
						interval: 1,
						inverse: is_bar ? true : false,
						axisLine: {
							show: false
						},
						axisTick: {
							alignWithLabel: false,
							show: true,
							length: 22 * (seq + 2),
							interval: function(index, value) {
								return value && value.charAt(0) == "|" ? true : false;
							}
						},
						axisLabel: {
							margin: 22 * (seq + 1) + 5,
							interval: 0,
							align: is_bar ? "right" : "center",
							padding: 0,
							formatter: function(value) {
								return value && value.charAt(0) == "|" ? value.substring(1) : value;
							}
						},
						splitLine: {
							show: true,
							interval: function(index, value) {
								return value && value.charAt(0) == "|" ? true : false;
							}
						},
						data: []
					};
					
					lc = 0;
					ppseq = -1;
					ppseqval = null;
					ppval = null;
					
					for (j=0; j < pxaxis.data.length; j++)
					{
						aval = pxaxis.data[j];
						sval = aval ? aval.split(sep) : null;
						t = "";
						if (sval && sval.length > i)
						{
							lc = sval.length > i+1 ? Math.max(lc, sval[i+1].length) : lc;
							ccval = sval.slice(0, i+1).join(sep);
							
							if (ccval != ppval)
							{
								if (ppseq > -1)
								{
									npos = Math.floor((cxaxis.data.length - ppseq - 1) / 2 + ppseq);
									if (cxaxis.data[npos])
									{
										cxaxis.data[npos] += ppseqval;
									}
									else
									{
										cxaxis.data[npos] = ppseqval;
									}
									ppseq = j;
									ppseqval = sval[i];;
								}
								else
								{
									ppseq = j;
									ppseqval = sval[i];;
								}
								
								t = "|";
							}
							ppval = ccval;
						} 
						cxaxis.data.push(t);
					}
					
					if (ppseq > -1 && ppseq <= cxaxis.data.length)
					{
						npos = Math.floor((cxaxis.data.length - ppseq - 1) / 2 + ppseq);
						if (cxaxis.data[npos])
						{
							cxaxis.data[npos] += ppseqval;
						}
						else
						{
							cxaxis.data[npos] = ppseqval;
						} 
					}
					
					if (is_bar)
					{
						pl += lc * 8 + 20;
						cxaxis.axisLabel.margin = pl;
						cxaxis.axisLabel.verticalAlign = "top";
						cxaxis.axisTick.length = pl + 50;
					}
					
					nxaxis.push(cxaxis);
					
					// adding dummy axis for the line separation
					nxaxis.push({
						position: pos,
						offset: is_bar ? pl - 5 : 22 * (seq + 1),
						axisLine: {
							show: true
						}
					});
				}
				
				if (is_bar)
				{
					coption.yAxis = nxaxis;
				}
				else
				{
					coption.xAxis = nxaxis;
				}
			}
		}																																					  

		if (!bfixedp && coption.renderas && coption.renderas.length > 0)
		{
			for (i=0; i < coption.renderas.length; i++)
			{
				if (coption.renderas[i] && series.length > i)
				{
					series[i].type = me.getSeriesType(coption.renderas[i], series[i], mcr);
					
					if (coption.dl_linetype_l && coption.dl_linetype_l[i])
					{
						series[i].lineStyle = series[i].lineStyle || {};
						series[i].lineStyle.type = coption.dl_linetype_l[i];
					}
					
					if (coption.dl_linewidth_l && coption.dl_linewidth_l[i])
					{
						series[i].lineStyle = series[i].lineStyle || {};
						series[i].lineStyle.width = Number(coption.dl_linewidth_l[i] || "1") || 1;
					}
				}
			}
		}
		
		if (bsync)
		{
			var xaxis = coption.xAxis = coption.xAxis || {};
			xaxis.crosshair = true;

			coption.axisPointer = {
				xAxisIndex: "all"
			};

			cchart.renderTo = null;
			cchart.marginLeft = 60; // Keep all charts left aligned
			cchart.zoomType = "x";
			
			if (dl_tooltip)
			{
				ctooltip = coption.tooltip = coption.tooltip || {};
				ctooltip.trigger = "axis";
				ctooltip.axisPointer = ctooltip.axisPointer || {};
				ctooltip.axisPointer.animation = false;
			}

			$.each(lblnames, function(k, lblname) {
				var cnt = $("<div class='i-sschart-cnt'></div>").appendTo(me.container),
					ocnt = $(me.container),
					owidth = IG$.j$ext._w(ocnt),
					oheight = IG$.j$ext._h(ocnt),
					oh = oheight / bsync_l,
					toption = $.extend(true, {}, coption),
					serie = [],
					i;

				IG$.j$ext._h(cnt, oh);
				IG$.j$ext._w(cnt, owidth);
				cnt.css({top: me.p_off});
				me.p_off += oh;

				toption.chart.renderTo = cnt[0];
				
				if (k < bsync_l - 1)
				{
					toption.legend.show = 0;
					if (toption.legend.bottom && toption.grid.bottom > 20)
					{
						toption.grid.bottom = 20;
					}
				}
				
				for (i=0; i < series.length; i++)
				{
					if (series[i].meas_k == k)
					{
						serie.push(series[i]);
						if (!series[i].type)
						{
							series[i].type = me.getSeriesType(rcs, series[i], mcr);
						}
					}
				}
				
				me.updateSeries(serie, mcr, coption);
				toption.series = serie;
				
				coptions.push(toption);
			});
		}
		else
		{
			if (rcs == "radar")
			{
				var rseries = [
						{
							name: "",
							type: "radar",
							data: []
						}
					];
				
				$.each(series, function(i, serie) {
					var m1 = Math.min.apply(null, serie.data),
						m2 = Math.max.apply(null, serie.data);

					if (!isNaN(m1))
					{
						vmin = isNaN(vmin) ? m1 : Math.min(m1, vmin);
					}

					if (!isNaN(m2))
					{
						vmax = isNaN(vmax) ? m2 : Math.max(m2, vmax);
					}

					rseries[0].data.push({
						name: serie.name,
						data_map: serie.data_map,
						value: serie.data,
						label: {
							formatter: function(param) {
								var d = param.dimensionIndex;
								return serie.data_map[d].fval;
							}
						}
					});
				});
				
				me.updateSeries(rseries[0], mcr, coption);
				series = rseries;
				
				if (!isNaN(vmin) && !isNaN(vmax))
				{
					for (i=0; i < coption.radar.indicator.length; i++)
					{
						coption.radar.indicator[i].min = Math.min(0, vmin);
						coption.radar.indicator[i].max = vmax;
					}
				}
			}
			else
			{
				for (i=0; i < series.length; i++)
				{
					if (!series[i].type)
					{
						series[i].type = me.getSeriesType(rcs, series[i], mcr);
					}
					
					me.updateSeries(series[i], mcr, coption);
				}
			}
			
			coption.series = series;
			
			if (mcr.datazoom && /(line|column|area)/.test(rcs))
			{
				coption.dataZoom = [
					{
						show: true,
						realtime: false
					}
				];
			}
			
			coptions.push(coption);
		}
				
		if (cf.length)
		{
			for (i=0; i < coptions.length; i++)
			{
				coptions[i].__cf = cf;
			}
		}
		
		for (i=0; i < coptions.length; i++)
		{
			coption = coptions[i];
			
			if (coption.visualMap && coption.visualMap.length)
			{
				for (j=0; j < coption.visualMap.length; j++)
				{
					vmap = coption.visualMap[j];
					var s = coption.series[j],
						b = 1;
						
					if (s && s.data && isNaN(vmap.min) && isNaN(vmap.max))
					{
						$.each(s.data, function(k, dt) {
							if (!isNaN(dt.value))
							{
								vmap.min = b ? dt.value : Math.min(dt.value, vmap.min);
								vmap.max = b ? dt.value : Math.max(dt.value, vmap.max);
								b = 0; 
							}
						});
					}
				}
			}
			
			if (/(line|column|area)/.test(rcs) && mcr.dl_xr_enabled && mcr.dl_xr_minc && (mcr.dl_xr_maxc || mcr.dl_xr_midc))
			{
				coption.visualMap = coption.visualMap || [];
				
				vmap = {
					type: "continuous",
					orient: "horizontal",
					right: 20,
					top: 5,
					show: mcr.dl_xr_hidden ? false : true,
					dimension: "x",
					// dimension: 0,
					// seriesIndex: [i],
					color: []
				};
				
				mcr.dl_xr_maxc && vmap.color.push(mcr.dl_xr_maxc);
				mcr.dl_xr_midc && vmap.color.push(mcr.dl_xr_midc);
				vmap.color.push(mcr.dl_xr_minc);
				
				vmap.min = 0;
				vmap.max = coption.xAxis.data.length;
				
				coption.visualMap.push(vmap);
			}
		}
		
		me.__coptions = coptions;

		if (!bfixedp && me.charttype !== "pointchart")
			me.render(coptions, mresult);

		return coptions;
	}
});