IG$.cVis.pointchart = $s.extend(IG$.cVis.cartesian, {
	charttype: "pointchart",
	subtype: null,
	
	initComponent: function() {
		var me = this;
		IG$.cVis.pointchart.superclass.initComponent.apply(me, arguments);
	},

	_prep_series_data: function(mresult) {
		var me = this,
			i, j,
			data = mresult._tabledata,
			fr = mresult.rowfix,
			fc = mresult.colfix,
			tc,
			hidden_columns = (me.hidden_columns = {}),
			isthumb = me.isthumb,
			mcr = me.chart_option,
			rcs = mcr.subtype,
			swapaxis = mcr.swapaxis,
			maxchartresult = mcr.maxchartresult,
			useformatvalue = mcr.useformatvalue,
			measures = me.sheetoption.model.measures,
			bsync = 0,
			bsync_l,
			bpolarchart = me.bpolarchart,
			vmin,
			vmax,
			series, spvalue,
			lblnames,
			xticks = (me.xticks = []),
			sep = mcr.xaxissep || ig$.chart_separator || IG$._separator,
			cfr, bdatas, _fc, n = 1;

		sep = sep == "nl" ? "\n" : sep;
			
		cfr = fr;
		bdatas = Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length));
		me._dlen = bdatas;

		tc = (data && data.length > 0) ? data[0].length : 0;

		series = me.series = [];

		if (fc+1 < tc)
		{
			var s,
				pvalue,
				bscatter = 0;

			bpointchart = 1;

			if (rcs == "bubble")
			{
				bscatter = 1;
				me.__bubblemax = NaN;
				me.__bubblemin = NaN;
			}
			else if (rcs == "scatter")
			{
				bscatter = 1;
			}

			if (swapaxis)
			{
				for (i=fc; i < Math.min(tc, (maxchartresult > 0 ? maxchartresult : tc)); i++)
				{
					if (hidden_columns[i])
						continue;
						
					var val = [me.mvalue(data[fr][i], useformatvalue), me.value(data[fr+1][i], useformatvalue)];

					if (bscatter && fr+2  < tr)
					{
						zvalue = me.mvalue(data[fr+2][i], useformatvalue);
						
						if(isNaN(me.__bubblemax))
						{
							me.__bubblemax = zvalue;
							me.__bubblemin = zvalue;
						}
						else
						{
							me.__bubblemax = Math.max(zvalue, me.__bubblemax);
							me.__bubblemin = Math.min(zvalue, me.__bubblemin);
						}
						val.push(zvalue);
					}

					var sname;
					
					for (j=0; j < fr; j++)
					{
						tlbl = me.fmtlbl(data[j][i].text);
						sname = (j == 0) ? tlbl : sname + sep + tlbl;
					}

					val.push(sname);
					val.push(j);

					cvalue = me.fmtlbl(data[0][j].text);

					if (cvalue != pvalue)
					{
						s = {
							name: cvalue,
							data: [val],
							meas_k: -1
						};
						series.push(s);
					}
					else if (s)
					{
						s.data.push(val);
					}

					pvalue = cvalue;
				}
			}
			else
			{
				for (i=fr; i < Math.min(data.length, (maxchartresult > 0 ? maxchartresult : data.length)); i++)
				{
					// var val = {x: me.mvalue(data[i][fc], useformatvalue), y: me.mvalue(data[i][fc+1], useformatvalue)};
					var val = [me.mvalue(data[i][fc], useformatvalue), me.mvalue(data[i][fc+1], useformatvalue)];

					if (bscatter && fc+2  < tc)
					{
						zvalue = me.mvalue(data[i][fc+2], useformatvalue);
						if (isNaN(me.__bubblemax))
						{
							me.__bubblemax = zvalue;
							me.__bubblemin = zvalue;
						}
						else
						{
							me.__bubblemax = Math.max(zvalue, me.__bubblemax);
							me.__bubblemin = Math.min(zvalue, me.__bubblemin);
						}
						val.push(zvalue);
					}

					var sname;
					for (j=0; j < fc; j++)
					{
						tlbl = me.fmtlbl(data[i][j].text);
						
						sname = (j == 0) ? tlbl : sname + sep + tlbl;
					}

					val.push(sname);
					val.push(i);

					cvalue = me.fmtlbl(data[i][0].text);

					if (cvalue != pvalue)
					{
						s = {
							name: cvalue,
							data: [val],
							meas_k: -1
						};
						series.push(s);
					}
					else if (s)
					{
						s.data.push(val);
					}

					pvalue = cvalue;
				}
			}
		}
		return series;
	},
	
	draw: function(mresult) {
		var me = this,
			coptions = IG$.cVis.pointchart.superclass.draw.apply(me, arguments),
			coption = coptions.length ? coptions[0] : null,
			cchart;
		
		if (coptions)
		{
			cchart = coption.chart;
			cchart.zoomType = "xy";

			coption.xAxis = {
				startOnTick: true,
				endOnTick: true,
				showLastLabel: true
			};
		}

		me.render(coptions, mresult);

		return coptions;
	}
});