IG$.cVis.fixedplacement = $s.extend(IG$.cVis.cartesian, {
	subtype: "fixedplacement",
	draw: function(mresult) {
		var me = this,
			coptions = IG$.cVis.fixedplacement.superclass.draw.apply(me, arguments),
			coption = coptions && coptions.length  ? coptions[0] : null;

		if (coption)
		{
			var s1 = 0,
				s2 = 0,
				barcolors = ["rgba(165,170,217,1)", "rgba(126,86,134,.9)", "rgba(248,161,63,1)", "rgba(186,60,61,.9)"];
			
			$.each(coption.series, function(i, serie) {
				var bpct = ((coption.series.length > 2 ? 30 : 20) + s1 * 10);
				serie.type = "bar";
				serie.barWidth = "" + bpct + "%";
				serie.barGap = "" + (- 100 - bpct / 2) + "%";
				serie.itemStyle = serie.itemStyle || {};
				serie.itemStyle.color = barcolors[i];
				s1++;
			});
		}
		else
		{
			IG$.ShowError(IRm$.r1("E_CHART_DRAWING") + " Pivot configuration error!");
		}

		me.render(coptions, mresult);
		
		return coptions;
	}
});