IG$.cVis.sunburst = $s.extend(IG$.cVis._echart, {
	subtype: "sunburst",

	draw: function(mresult) {
		var me = this,
			container = $(me.container),
			box,
			mcr = me.sheetoption ? me.sheetoption.model.chart_option : null,
			data = mresult._tabledata,
			fc = mresult.colfix,
			fr = mresult.rowfix,
			tc = (data.length > 0) ? data[0].length : 0,
			i, j, k, lvl,
			dl_position = mcr.dl_position,
			dl_enable = mcr.dl_enable,
			dl_tooltip = mcr.dl_tooltip,
			titleposition = mcr.titleposition || "",
			sb_l_min = mcr.sb_l_min || 0,
			sundata = {name: "root", children: []},
			pva = [],
			dcol = fc,
			min, max,
			bn = 0,
			mobj, cval, tval, 
			ctitle, cgrid,
			sum = 0,
			option = {
				visualMap: {
					type: "continuous",
					inRange: {
						color: ["#2F93C8", "#AEC48F", "#FFDB5C", "#F98862"]
					}
				},
				series: {
					type: "sunburst",
					radius: [(mcr.pieinnerradius || 0) + "%", (mcr.pieouterradius || "90") + "%"],
					label: {
						rotate: "radial",
						show: dl_enable,
						silent: true
					}
				}
			};

		if (fc < 2) 
		{
			IG$.ShowError(IRm$.r1("E_CHART_DRAWING") + " Need 2 Column on pivot definition!");
			return;
		}
			
		for (i=0; i < fc; i++)
		{
			pva.push({name: null, child: sundata});
		}

		for (i=fr; i < data.length; i++)
		{
			for (j=0; j < fc; j++)
			{
				cval = data[i][j].code;
				tval = data[i][j].text || cval;
				
				if (pva[j].name != tval)
				{
					if (j == 0)
					{
						mobj = {
							name: tval
						};
						sundata.children.push(mobj);
						pva[j].child = mobj;
						pva[j].tval = null;
					}
					else
					{
						mobj = {
							name: tval
						};
						
						if (j == fc -1)
						{
							mobj.value = Number(data[i][dcol].code || 0);
							mobj.value = isNaN(mobj.value) ? 0 : mobj.value;
							mobj.fval = data[i][dcol].text || data[i][dcol].code;
						}
						
						if (j == fc - 1 && sb_l_min > 0 && mobj.value < sb_l_min)
						{
							// ignore this drawing
						}
						else
						{
							pva[j-1].child.children.push(mobj);
						}
						pva[j].child = mobj;
						pva[j].tval = null;
					}

					pva[j].name = mobj.name;

					if (j == fc - 1)
					{
						sum += mobj.value;
						
						if (!bn)
						{
							min = mobj.value;
							max = mobj.value;
							bn = 1;
						}
						else
						{
							min = Math.min(min, mobj.value);
							max = Math.max(max, mobj.value);
						}
						
						for (k = j - 1; k >=0; k--)
						{
							if (pva[k].tval)
							{
								pva[k].tval += mobj.value;
							}
							else
							{
								pva[k].tval = mobj.value;
							}
							
							if (isNaN(pva[k].child.value))
							{
								pva[k].child.value = mobj.value;
							}
							else
							{
								pva[k].child.value += mobj.value;
							}
							
							min = Math.min(min, pva[k].tval);
							max = Math.max(max, pva[k].tval);
						}
					}
					else
					{
						mobj.children = [];
					}
				}
			}
		}

		if (dl_tooltip)
		{
			option.tooltip = option.tooltip || {};
			option.tooltip.formatter = function(params, ticket, callback) {
				var pct = params.percent;
				
				if (!pct)
				{
					pct = (params.data.value) / (sum) * 100;
				}
				
				if (mcr)
				{
					var piepctprec = mcr.piepctprec;
					
					if (typeof(piepctprec) != "undefined" && piepctprec > -1)
					{
						pcval = pct * Math.pow(10, piepctprec);
						pcval = Math.round(pcval);
						pcval = pcval / Math.pow(10, piepctprec);
						
						pct = pcval;
					}
				}
				
				return params.name + "<br>" + (params.data.fval || IG$.NumberFormat("#,###", params.data.value)) + " (" + pct + " %)";
			}
		}
		
		me.adjOption(option);
		me.drawmode = "echart";
		
		var removeSubValue = function(items) {
			var i;
			for (i=items.length-1; i >= 0; i--)
			{
				if (items[i].value < sb_l_min)
				{
					items.splice(i, 1);
				}
				else if (items[i].children && items[i].children.length)
				{
					removeSubValue(items[i].children);
				}
			}
		};
		
		if (sb_l_min > 0)
		{
			removeSubValue(sundata.children);
		}
		
		option.series.data = sundata.children;
		option.visualMap.min = min;
		option.visualMap.max = max;
				
		if (dl_position == "outside")
		{
			option.series.levels = [{}];
			for (i=0; i < fc; i++)
			{
				lvl = {};
				if (i == fc - 1)
				{
					lvl.label = {
						position: "outside",
						padding: 3,
						show: dl_enable
					};
				}
				option.series.levels.push(lvl);
			}
		}

		me.render([option], mresult);

		return [option];
	}
});